.home {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  max-width: 1500px;
}

.home__row {
  display: flex;
  z-index: 1;
  margin: 0 5px;
}

.home__image {
  width: 100%;
  z-index: -1;
  margin-bottom: -150px;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

@media only screen and (max-width: 630px) {
  .home__image {
    width: 100vw;
    z-index: -1;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
    object-fit: cover;
  }

  .home__row {
    display: flex;
    flex-wrap: wrap;
  }

  .home {
    display: flex;
    justify-content: center;
  }
}
